* {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  background: linear-gradient(180deg, #3D5067, #000);
  font-family: 'Work Sans', sans-serif;
  font-size: 1.4rem;
  margin: 0;
  padding: 0;
}

nav ul {
  list-style-type: none;
  padding: 0;
}

.center {
  align-items: center;
  display: flex;
  flex-direction: column;
}

.App {
  display: flex;
  flex-direction: column;
}

.App-top {
  display: flex;
  height: calc(100vh - 90px);
}

.Login {
  background-color: #FFF;
  height: 100vh;
  width: 100vw;
}

.App-title {
  color: #FFF;
}

.Navbar {
  background-color:rgba(0, 0, 0, 0.7);
  color: #B0AFAF;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 15px 25px;
  width: 220px;
}

.navbar-main svg {
  height: 25px;
  margin-right: 12px;
  width: 25px;
}

.navbar-main a {
  color: #B0AFAF;
  font-size: 1.6rem;
  font-weight: bold;
  margin-bottom: 30px;
  text-decoration: none;
}

.navbar-main a:hover {
  color: #FFF;
  cursor: pointer;
}

.navbar-main .selected,
.navbar-main .selected:hover {
  color: #2EB925;
}

.navbar-main {
  display: flex;
  flex-direction: column; 
}

.navbar-main h2 {
  font-size: 1.2rem;
  font-weight: normal;
  letter-spacing: 0.1rem;
  margin: 0;
  text-transform: uppercase;
}

.navbar-main li {
  margin-bottom: 15px;
}

.navbar-main .album-name {
  margin-bottom: 5px;
}

.navbar-main .album-name:hover {
  color: #FFF;
  cursor: pointer;
}

.navbar-main .album-type {
  font-size: 0.8em;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.logout {
  background: none;
  border-radius: 20px;
  border-width: 2px;
  color: #B0AFAF;
  padding: 5px;
  text-transform: uppercase;
}

.logout:hover {
  border-color: #FFF;
  color: #FFF;
  cursor: pointer;
}
.App-main {
  flex-grow: 1;
  overflow-y: auto;
}

.App-main h2 {
  text-align: center;
}

.Browse {
  color: #FFF;
  width: 80%;
  margin: 0 auto;
}

.Browse h2 {
  font-size: 3.6rem;
  margin-bottom: 0;
}

.category-nav ul {
  font-size: 1.4rem;
  display: flex;
  justify-content: center;
  letter-spacing: 0.08rem;
}

.category-nav li {
  font-weight: 500;
  padding: 10px;
  margin: 5px;
  text-transform: uppercase;
}

.category-nav a {
  color: #B0AFAF;
  padding-bottom: 5px;
  text-decoration: none;
}

.category-nav a:hover {
  color: #FFF;
}

.category-nav a.selectedCategory {
  border-bottom: 2px solid #2EB925;
  color: #FFF;
}

.tilelist {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  list-style: none;
  margin: 0;
  padding: 0;

  display: grid;
  grid-template-columns: repeat(6, auto);
  grid-template-rows: auto;
}

.tile {
  align-items: center;
  display: flex;
  flex-direction: column;
  line-height: 1.8rem;
  margin: 20px 8px;
  position: relative;
  text-align: center;
  width: 150px;
}

.thumbnail {
  margin-bottom: 10px;
}

.thumbnail img {
  box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.25);
  height: 150px;
}

.overlay {
  background-color: rgba(0, 0, 0, 0.6);
  display: none;
  height: 150px;
  position: absolute;
  top: 0;
  width: 150px;
}

.thumbnail:hover .overlay {
  align-items: center;
  cursor: pointer;
  display: flex;
  justify-content: center;
}

.thumbnail:hover .overlay svg {
  height: 70px;
  opacity: 0.95;
  width: 70px;
}

.tile .title:hover {
  cursor: pointer;
}

.albumName {
  font-weight: 500;
  line-height: 1.5;
}

.artistName {
  color: #B0AFAF;
  line-height: 1.5;
}

.artistName:hover {
  color: #FFF;
  cursor: pointer;
  text-decoration: underline;
  -webkit-text-decoration-skip: ink;
          text-decoration-skip: ink;
}

.Controls {
  align-items: center;
  color: #B0AFAF;
  display: flex;
  flex-direction: column;
  font-size: 1.2rem;
  padding: 10px;
}

.Controls-buttons button {
  background: none;
  border: none;
  margin: 3px;
}

.Controls-buttons button:hover {
  cursor: pointer;
}

.Controls-buttons svg {
  color: #B0AFAF;
  height: 15px;
  width: 15px;
}

.Controls-buttons svg:hover {
  color: #FFF;
}

.Controls-buttons .play-pause svg {
  height: 40px;
  width: 40px;
}

.Controls-progress {
  display: flex;
  align-items: center;
}

.progress-time,
.progress-time-total {
  width: 26px;
}

.progress-bar {
  background-color: #4B4B4B;
  border-radius: 8px;
  height: 4px;
  margin: 8px;
  width: 400px;
}

.progress-bar-completed {
  background-color: #B0AFAF;
  border-radius: 8px;
  height: 4px;
}

.player {
  background-color: #2A2A2A;
  display: flex;
  height: 90px;
}

.currently-playing-container {
  width: 400px;
}

.currently-playing-container span {
  display: flex;
  padding: 17px 15px;
}

.currently-playing-container img {
  height: 56px;
  margin-right: 15px;
  width: 56px;
}

.currently-playing-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-width: 0;
}

.currently-playing-info p {
  line-height: 2rem;
  margin: 0;
}

.currently-playing-title {
  color: #FFF;
  font-size: 1.3rem;
}

.currently-playing-artist {
  color: #B0AFAF;
  font-size: 1.1rem;
}

.currently-playing-title:hover,
.currently-playing-artist:hover {
  color: #FFF;
  cursor: pointer;
  text-decoration: underline;
  -webkit-text-decoration-skip: ink;
          text-decoration-skip: ink;
}

.truncate {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.Search {
  background-color: #121212;  
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  width: 100%;
}

.searchbar {
  background-color: #2A2A2A;
  display: flex;
  flex-direction: column;
  height: 150px;
  justify-content: center;
  padding-left: 25px;
  padding-right: 25px;
}

.searchbar label {
  color: #FFF;
  margin-bottom: 5px;
}

input {
  background: none;
  border: none;
  color: #FFF;
  caret-color: #2EB925;
  font-size: 4.5rem;
  height: 6rem;
}

input::-webkit-input-placeholder {
  color: #4B4B4B;
  font-weight: 700;
}

input::-ms-input-placeholder {
  color: #4B4B4B;
  font-weight: 700;
}

input::placeholder {
  color: #4B4B4B;
  font-weight: 700;
}

.results-container {
  color: #FFF;
  height: calc(100% - 200px);
  margin-left: 30px;
  margin-right: 30px;
}

.SearchResults h2 {
  font-size: 3.6rem;
  text-align: center;
}

.top-results,
.artist-results,
.albums-results,
.playlists-results {
  padding-bottom: 20px;
}

.top-results {
  display: flex;
}

.top-results__playlist {
  align-items: center;
  display: flex;
  flex-direction: column;
  position: relative;
  width: 300px;
}

/* original overlay styles in Browse.css */
.thumbnail.large {
  height: 250px;
  width: 250px;
}

.thumbnail.large .overlay {
  height: 250px;
  width: 250px;
}

.thumbnail.circle .overlay {
  border-radius: 50%;
}

.top-results__playlist img {
  height: 250px;
  width: 250px;
}

.top-results__playlist img:hover,
.top-results__playlist-details--name:hover {
  cursor: pointer;
}

.top-results__playlist-details {
  display: flex;
  flex-direction: column;
  line-height: 1.5;  
  margin: 10px;
  text-align: center;
}

.top-results__playlist-details--name, 
.artist-results__artist,
.albums-results__album,
.playlists-results__playlist {
  font-weight: 500;
}

.playlists-results__playlist:hover,
.artist-results__artist:hover {
  cursor: pointer;
}

.top-results__playlist-details--owner,
.albums-results__artist,
.playlists-results__owner {
  color: #B0AFAF;
}

.top-results__playlist-details--owner:hover,
.top-results__tracks--artist:hover,
.top-results__tracks--album:hover,
.albums-results__artist:hover,
.playlists-results__owner:hover {
  color: #FFF;
  cursor: pointer;
  text-decoration: underline;
  -webkit-text-decoration-skip: ink;
          text-decoration-skip: ink;
}

.top-results__tracks {
  flex-grow: 1;
  min-width: 0;
}

.top-results__tracks ul {
  list-style: none;
  margin: 0;
}

.top-results__tracks ul li {
  display: flex;
  height: 60px;
  justify-content: space-between;
  padding: 12px;
}

.top-results__tracks ul li:hover {
  cursor: pointer;
}

.top-results__tracks-left {
  align-items: baseline;
  display: flex;
  min-width: 0;
}

.top-results__tracks--playStatus {
  margin-right: 15px;
}

.music-note {
  color: #B0AFAF;  
}

.top-result-track .play-icon,
.top-result-track:hover .music-note {
  display: none;
}

.top-result-track:hover .play-icon {
  display: inline;
}

.top-results__tracks--name {
  font-size: 1.6rem;
  line-height: 1.5;
  margin-right: 10px;
}

.top-results__tracks--artist-info {
  color: #B0AFAF;
  display: flex;
  margin-right: 10px;
}

.top-results__tracks--artist {
  margin-right: 3px;
}

.top-results__tracks--album {
  margin-left: 3px;
  margin-right: 3px;
}

.top-results__tracks--duration {
  color: #B0AFAF;
  font-size: 1.6rem;  
}

.thumbnail {
  width: 150px;
  height: 150px;
}

.artist.thumbnail {
  border-radius: 100%;
}

.truncate {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.album-wrapper,
.playlist-wrapper {
  display: flex;
  overflow-y: auto;
  width: 100%;
}

.album-details,
.playlist-details {
  display: flex;
  flex-direction: column;
  line-height: 3.2rem;
  padding: 40px 20px 40px 40px;
  text-align: center;
  width: 300px;
}

.album-details img,
.playlist-details img {
  height: 250px;
  width: 250px;
}

.album-details--title,
.playlist-details--title {
  color: #FFF;
  font-size: 2.5rem;
  font-weight: 700;
  padding-top: 15px;
}

.album-details--artist,
.playlist-details--owner {
  color: #B0AFAF;
}

.album-details--other,
.playlist-details--other {
  color: #B0AFAF;
  text-transform: uppercase;
}

.Playlist {
  color: #FFF;
  flex-grow: 1;
  font-size: 1.6rem;
  padding-right: 30px;
  padding-top: 25px;
  width: 50%;
}

.Playlist ul {
  list-style-type: none;
  padding: 0;
}

.playlist-track {
  align-items: center;
  display: flex;
  height: 60px;
  justify-content: space-between;
  padding-left: 15px;
  padding-right: 15px;
}

.playlist-track:hover {
  background-color: rgba(0,0, 0, 0.3);
  cursor: pointer;
}

.playlist-track-left {
  display: flex;
  min-width: 0;
}

.playlist-track--playStatus {
  margin-right: 15px;
}

.music-note {
  color: #B0AFAF;  
}

.playlist-track .play-icon,
.playlist-track:hover .music-note {
  display: none;
}

.playlist-track:hover .play-icon {
  display: inline;
}

.playlist-track--title {
  margin-right: 10px;
}

.playlist-track--artist-info {
  color: #B0AFAF;
  font-size: 1.4rem;
}

.playlist-track--artist, 
.playlist-track--additionalArtists {
  display: inline-block;
}

.playlist-track--artist {
  margin-right: 3px;
}

.playlist-track--additionalArtists {
  margin-left: 3px;
  margin-right: 3px;
}

.album-details--artist:hover, 
.playlist-details--owner:hover,
.playlist-track--artist:hover, 
.playlist-track--additionalArtists:hover {
  color: #FFF;
  cursor: pointer;
  text-decoration: underline;
  -webkit-text-decoration-skip: ink;
          text-decoration-skip: ink;
}

.playlist-track--duration {
  color: #B0AFAF;
}

.truncate {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.NoMatch {
  align-items: center;
  color: #FFF;
  display: flex;
  font-size: 2rem;
  margin: 0 auto;
}
